.chats{
    position: relative;
    height: 400px;
    width: 250px;
    background-color: black;
}
.chats_header{
    background-color: #059ee0;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
    }
.chats_search{
    display: flex;
    align-items: center;
    flex: 1;
    padding-left: 8px;
   }
.chats_search > input{
    color: white;
    border: none;
    outline: none;
    background-color: transparent;
    font-size: 12px;
    flex: 1;
}
.chats_search > input::placeholder{
    color: white;
    opacity: 1;
}
.chats_icon{
    color: white;
    font-size: 18px !important;
}
.chats_icons{
    color: white;
    font-size: 13px !important;
}
.chats_avatar{
    width: 25px !important;
    height: 25px !important;
}
.chats_post{
    box-shadow: 1px -7px 7px -6px rgba(0, 0,0, 0.44);
    height: 359px;
    margin-top: -9px;
    background-color: white;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    overflow: scroll;
}
.chats_post::-webkit-scrollbar{
    display: none;
}
.chats_post{
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.chats_takePicIcon{
    position: absolute;
    background-color: white;
    border-radius: 200px;
    color: gray;
    font-size: 40px !important;
    cursor: pointer;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, -50%);
}
.chats_takePicIcon:hover{
    opacity: 0.8;
}
    
    